@import "mixins";


.comments {
    position: relative;
    width: 100%;
    height: 700px;
    @include tablet {
        height: auto;
        padding: 60px 0;
    }
    // .comments__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        @include tablet {
            display: block;
        }
    }

    // .comments__text

    &__text {
        height: 100%;
        padding-top: 150px;
        @include tablet {
            padding: 0;
            margin: 0 0 30px 0;
        }
    }

    // .comments__suheader

    &__suheader {
        color: $lightGreyColor;
        margin: 0 0 112px 0;
        letter-spacing: 0.08em;
        @include mobile {
            color: #fff;
            margin: 0 0 50px 0;
        }
    }

    // .comments__title

    &__title {
        color: #fff;
        max-width: 435px;
        @include mobile {
            font-size: 36px;
            line-height: 130%;
            
        }
    }
    

    // .comments__block

    &__block {
        position: relative;
        width: 600px;
        height: 100%;
        margin: 0 40px 0 0;
        background: rgba(0, 0, 0, 0.6);
        padding: 150px 90px 0 90px;
        @include tablet {
            width: 100%;
        }
        @include mobile {
           padding: 60px 15px 0 15px;
            
        }
    }

    // .comments__slider

    &__slider {
        width: 100%;
        @include tablet {
            height: 460px;
        }
    }

    // .comments__background

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-image: url("../img/comments/background.png");
        background-position: center;
        background-size: cover;
        z-index: -50;
    }
}




// Slider
.comments-slider-counter {
    bottom: -105px;

    @include tablet {
        bottom: 120px;
      }
      @include mobile {
        bottom: 60px;
      }
    // .comments-slider-counter__current

    &__current {
        color: #fff !important;
    }
}

.comments-slider {
   
    position: relative;
    // .comments-slider__item

    &__item {
        width: 100%;
    }

    // .comments-slider__text

    &__text {
        color: $greyColor;
        margin: 0 0 60px 0;
        @include mobile {
           font-size: 14px;
           margin: 0 0 40px 0;
         }
    }

    // .comments-slider__people

    &__people {
        @include flex-v-center;
    }

    // .comments-slider__photo

    &__photo {
        width: 51;
        height: 51px;
        margin: 0 20px 0 0;
    }

    // .comments-slider__description

    &__description {

    }

    // .comments-slider__name

    &__name {
        color: #fff;
        letter-spacing: 0.05em;
        margin: 0 0 10px 0;
    }

    // .comments-slider__service

    &__service {
        font-size: 14px;
        line-height: 15px;
        letter-spacing: 0.05em;
        color: #C2C2C2;
    }
}
.comments-slider .slick-arrow{
    position: absolute;
    bottom: -105px;
    z-index: 10;
    font-size: 0;
    outline: none;
    border: none;
    width: 45px;
    height: 10px;
    cursor: pointer;
    transition: .2s all linear 0s;
    @include tablet {
        bottom: 120px;
      }
      @include mobile {
        bottom: 60px;
      }
    @include desktop {
        &:hover {
            transform: translateY(-1px);
        }
    }
}
.comments-slider .slick-arrow.slick-prev{
    left: 0;
    background: url("../img/comments-arrow-right.svg") 0 0 no-repeat;
}
.comments-slider .slick-arrow.slick-next{
    left: 90px;   
    background: url("../img/comments-arrow-left.svg") 0 0 no-repeat;
}