@import "mixins";


.contact {
    width: 100%;
    padding: 127px 0 140px 0;
    @include mobile {
        display: block;
        padding: 80px 0;
     }
    // .contact__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        @include mobile {
           display: block;
        }
    }

    // .contact__texts

    &__texts {
        margin: 0 30px 0 0;
    }

    // .contact__title

    &__title {
        margin: 0 0 20px 0;
        max-width: 223px;
        @include mobile {
            font-size: 24px;
         }
    }
 
    // .contact__subtitle

    &__subtitle {
        @include mobile {
            margin: 0 0 50px 0;
         }
    }

    // .contact__form

    &__form {
        max-width: 423px;
        margin: 0 110px 0 0;
       @include tablet2 {
           margin: 0;
       }
       @include mobile {
            max-width: 100%;
            width: 100%;
       }
    }

    // .contact__items

    &__items {
        margin: 0 0 20px 0;
        width: 100%;
    }

    // .contact__item

    &__item {
        width: 100%;
        display: flex;
        flex-direction: column;
        span {
            color: $greyColor;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            font-size: 12px;
            line-height: 20px;
            margin: 0 0 5px 0;
        }
    }

    // .contact__input

    &__input {
        width: 100%;
        outline: none;
        border: none;
        letter-spacing: 0.05em;
        font-size: 18px;
        padding-bottom: 10px;
        border-bottom: 1px solid $lightGreyColor;
        margin: 0 0 30px 0;
        color: $darkColor;
        &:focus {
            border-bottom: 1px solid $greenColor;
           
        }
        &:valid {
           
        }
    }

    // .contact__row

    &__row {
        width: 100%;
        @include flex-all-sb;
        @include mobile {
           display: block;
       }
    }

    // .contact__checkbox

    &__checkbox {
        position: relative;
        max-width: 300px;
        div {
            padding-left: 35px;
        }
    }

    // .contact__content

    &__content {
        cursor: pointer;
       
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          border: 1px solid #000;
          width: 20px;
          height: 20px;
          box-sizing: border-box;
          background: transparent;
          border: 1px solid rgba(194, 194, 194, 0.3);
          border-radius: 2px;
        }
    
        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 20px;
          height: 20px;
          box-sizing: border-box;
          background-image: url("../img/check.png");
          background-position: center;
          background-size: 10px 10px;
          background-repeat: no-repeat;
          opacity: 0;
          transition: opacity 0.3s ease-in-out;
        }
        a {
            color: $greenColor;
            text-decoration: underline;
        }
    }

    // .contact__field

    &__field {
        opacity: 0;
        appearance: none;
        position: absolute;
    }

    // .contact__button

    &__button {
        width: 160px;
        @include mobile {
            width: 100%;
            margin: 30px 0 0 0;
        }
    }
}

.contact__field:checked + .contact__content::after {
    opacity: 1;
  }
  
  .contact__field:focus + .contact__content::before {
  }
  
  .contact__field:disabled + .contact__content {
    opacity: 0.4;
    pointer-events: none;
  }
  



// Form validation
.js-validate-error-label {
    transform: translateY(-20px);
    color: rgb(240, 34, 34) !important;
    font-size: 13px !important;
}
  
.js-validate-error-field {
    border-bottom: 1px solid rgb(240, 34, 34) !important;
}
  