@import "mixins";


.cost {
    width: 100%;
    background-color: $lightColor;
    padding: 130px 0;
    @include mobile {
        padding: 80px 0 60px 0;
    }

    // .cost__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .cost__title

    &__title {
        text-align: center;
        margin: 0 0 60px 0;
        @include mobile {
            font-size: 24px;
        }
    }

    // .cost__row

    &__row {
        @include flex-all-sb;
        @include tablet2 {
            display: block;
        }
    }

    // .cost__item

    &__item {
        width: 48%;
        min-height: 564px;
        background: #FFFFFF;
        border-radius: 4px;
        padding: 45px 45px 60px 45px;
        flex-direction: column;
        @include flex-v-center;
        @include tablet2 {
            width: 100%;
            margin: 0 0 20px 0;
        }
        @include mobile {
            padding: 30px 24px;
            min-height: 446px;
        }
    }

    // .cost__top

    &__top {
        flex-direction: column;
        @include flex-v-center;
        margin: 0 0 54px 0;
        @include mobile {
            margin: 0 0 40px 0;
        }
    }

    // .cost__illustration

    &__illustration {
        margin: 0 0 20px 0;
        @include mobile {
            width: 40px;
            height: 40px;
        }
    }

    // .cost__caption

    &__caption {
        text-align: center;
        @include mobile {
            font-size: 18px;
        }
    }

    // .cost__columns

    &__columns {
        width: 100%;
        margin: 0 0 63px 0;
        @include mobile {
           margin: 0;
        }
    }

    // .cost__line

    &__line {
        @include flex-all-sb;
        width: 100%;
        padding-bottom: 25px;
        border-bottom: 1px solid $lightGreyColor;
        margin: 0 0 24px 0;
        &:last-child {
            margin: 0;
            @include mobile {
                margin: 0 0 20px 0;
             }
        }
        @include mobile {
            margin: 0 0 20px 0;
            padding-bottom: 20px;
         }
    }

    // .cost__description

    &__description {
        @include mobile {
            max-width: 136px;
            font-size: 14px;
         }
    }

    // .cost__price

    &__price {
       color: $greenColor;
        letter-spacing: 0.02em;
        font-size: 16px;
        @include mobile {
            font-size: 14px;
         }
    }

    // .cost__button

    &__button {
        width: 251px;
        @include mobile {
            width: 100%;
         }
    }
}
