@import "mixins";


.footer {
    width: 100%;
    background-color: #282828;
    // .footer__main

    &__main {
        width: 100%;
        height: 100%;
        padding: 50px 0 64px 0;
        @include tablet {
            padding: 40px 0 20px 0;
        }
    }

    // .footer__row

    &__row {
        width: 100%;
        height: 100%;
        
        display: flex;
        justify-content: space-between;
        @include mobile {
            flex-direction: column;
            align-items: center;
        }
    }

    // .footer__general

    &__general {
        display: flex;
        flex-direction: column;
        max-width: 140px;
        @include mobile {
            align-items: center;
            margin: 0 0 54px 0;
            max-width: 100%;
        }
    }

    // .footer__logo

    &__logo {
        width: 67px;
        height: 67px;
        margin-bottom: 48px;
        @include mobile {
            margin-bottom: 28px;
        }
    }

    // .footer__map

    &__map {
        font-size: 12px !important;
        margin-bottom: 24px;
        text-decoration: underline;
        transition: .3s all ease 0s;
        &:hover {
            color: #fff;
        }
        @include mobile {
            margin-bottom: 14px;
            
        }
    }

    // .footer__copy

    &__copy {
        font-size: 12px !important;
    }

    // .footer__services

    &__services {
        display: flex;
        flex-direction: column;
        max-width: 300px;
        @include tablet {
            display: none;
        }
    }

    // .footer__subheader

    &__subheader {
        color: #fff !important;
        margin-bottom: 39px;
    }

    // .footer__itemsservices

    &__itemsservices {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        a {
            width: 50%;
            margin: 0 0 24px 0;
            &:hover {
                text-decoration: underline;
            }
        }
    }

    // .footer__item

    &__item {
        color: #fff !important;
        font-size: 14px !important;
    }

    // .footer__contacts

    &__contacts {
        display: flex;
        flex-direction: column;
        max-width: 330px;
        @include mobile {
            align-items: center;
            margin: 0 0 40px 0;
            max-width: 100%;
        }
    }

    // .footer__numbers

    &__numbers {
        display: flex;
        flex-direction: column;
        margin: 0 0 25px 0;
    }

    // .footer__address

    &__address {
        max-width: 300px;
        color: #fff !important;
        font-size: 14px !important;
    }

    // .footer__more

    &__more {
        display: flex;
        flex-direction: column;
        max-width: 235px;
        @include mobile {
            align-items: center;
            max-width: 100%;
        }
    }

    // .footer__email

    &__email {
        text-decoration: underline;
        font-size: 24px;
        color: #fff;
        letter-spacing: 0.03em;
        margin: 0 0 40px 0;
       
    }

    // .footer__networks

    &__networks {
        @include flex-all-sb;
        margin: 0 0 25px 0;
        @include mobile {
            width: 128px;
        }
    }

    // .footer__social

    &__social {
        font-size: 12px !important;
        @include mobile {
            display: none;
        }
    }

    // .footer__ico

    &__ico {
        cursor: pointer;
    }

    // .footer__bottom

    &__bottom {
        
    }

    // .footer__button

     &__button {
        width: 100%;
        @include mobile {
            width: 235px;
        }
    }
}

.footer-bottom {
    width: 100%;
    height: 55px;
    @include tablet {
        height: auto;
    }
    border-top: 1px solid rgba(0, 0, 0, 0.25);;
    // .footer-bottom__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        @include tablet {
            padding: 15px 0;
            flex-direction: column;
            align-items: center;
        }
    }

    // .footer-bottom__pay

    &__pay {
        @include flex-v-center;
        @include tablet {
           margin-bottom: 20px;
        }
        @include mobile {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    // .footer-bottom__text

    &__text {
        text-decoration: underline;
        margin: 0 25px 0 0;
        font-size: 12px !important;
        @include mobile {
            margin: 0 0 10px 0;
        }
    }

    // .footer-bottom__methods

    &__methods {
    }

    // .footer-bottom__ico

    &__ico {
        margin: 0 15px 0 0;
        cursor: pointer;
    }

    // .footer-bottom__links

    &__links {
        @include mobile {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    // .footer-bottom__link

    &__link {
        font-size: 12px !important;
        text-decoration: underline;
        margin: 0 0 0 30px;
        transition: .3s all ease 0s;
        text-align: center;
        @include mobile {
            margin: 0 0 10px 0;
        }
        &:hover {
            color: #fff;
        }
    }
}
