@import "mixins";

.menu.active {
    left: 0;
}
.menu {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: -100%;
    background-color: $darkColor;
    z-index: 49;
    transition: .4s all ease 0s;
    overflow-y: auto;
    // .menu__body

    &__body {
        width: 100%;
        display: flex;
        justify-content: center;
        @include mobile {
           display: block;
        }
    }

    // .menu__item

    &__item {
        margin: 0 70px 0 0;
        @include mobile {
            margin: 0;
            width: 100%;
           display: flex;
           align-items: center;
           flex-direction: column;
            
        }
    }

    // .menu__link

    &__link {
        color: #fff;
        @include mobile {
            width: 100%;
            @include flex-all-center;
            font-size: 15px;
            padding: 30px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        }
    }

    // .menu__contacts

    &__contacts {
        @include mobile {
            width: 100%;
            @include flex-all-center;
            padding: 30px;
        }
    }

    // .menu__contact

    &__contact {
        @include mobile {
          margin: 0 10px;
        }
    }

    // .menu__container

    &__container {
        width: 100%;
        height: 100%;
        @include flex-all-center;
        @include mobile {
           display: block;
           padding-top: 74px;
        }
    }
}


// Item
.menu-item {
    @include mobile {
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    }
	// .menu-item__container

    &__container {
        @include mobile {
          display: block;
        }
    }	

    // .menu-item__title

    &__title {
        width: 100%;
        @include flex-all-center;
        color: #fff;
        position: relative;
        margin: 0 0 20px 0;
        @include mobile {
           font-size: 15px;
           padding: 30px 0;
           margin: 0;

        //    &::before {
        //         content: "";
        //         background-image: url("../img/menu-arrow.svg");
        //         position: absolute;
        //         width: 9px;
        //         height: 5px;
        //         right: 0;
        //    }
          
        }
    }

    // .menu-item__content

    &__content {
        @include mobile {
            display: none;
        }
        margin: 0;
        ul {
            @include flex-v-center;
            flex-direction: column;
        }
        li {
            margin: 0 0 10px 0;
            @include mobile {
                margin: 0 0 30px 0;
            }
        }
    }

    // .menu-item__item

    &__item {
        color: #fff;
        transition: .2s all linear 0s;
        @include mobile {
            font-size: 15px;
          }
        &:hover {
            text-decoration: underline;
        }
    }
}

