@import "mixins";


.services {
    width: 100%;
    background-color: $lightColor;
    padding: 80px 0 143px 0;
    @include mobile {
        padding: 60px 0 80px 0;
    }
    // .services__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .services__top

    &__top {
        @include flex-all-sb;
        width: 100%;
        height: 457px;
        margin: 0 0 15px 0;
        @include container {
            display: block;
            height: auto;
        }
    }

    // .services__big

    &__big {
        width: 50%;
        height: 100%;
        margin: 0 15px 0 0;
        @include container {
            width: 100%;
            margin: 0 0 15px 0;
        }
        @include mobile {
            height: 350px !important;
         }
        img {
            min-width: 244px;
            @include container {
                min-width: 300px;
            }
            @include mobile {
                min-width: 0;
                width: 142px;
            }
        }
    }

    // .services__block

    &__block {
        width: 50%;
        @include container {
            width: 100%;  
        }
       
       &>div {
            @include mobile {
                height: 250px;
            }
            &:first-child {
                margin: 0 0 15px 0;
            }
       }
    }

    // .services__item

    &__item {
    }

    // .services__bottom

    &__bottom {
        width: 100%;
        height: 222px;
        @include flex-all-sb;
        @include container {
            display: block;
            height: auto;
        }
        &>div {
            width: 33%;
            margin: 0 15px 0 0;
            @include container {
              width: 100%;
              margin: 0 0 15px 0;
              height: 150px;
            }
            &:last-child {
                margin: 0;
            }
        }
    }
}


.services-item {
    height: 100%;
    background-color: #fff;
    border-radius: 4px;
    @include flex-all-sb;

    // .services-item__content

    &__content {
        padding: 30px 35px 30px 40px;
        @include mobile {
            padding: 20px 20px 20px 30px;
        }
    }

    // .services-item__title

    &__title {
        margin: 0 0 15px 0;
        @include mobile {
            font-size: 18px;
          }
    }

    // .services-item__text

    &__text {
        color: $greyColor;
        @include container {
            max-width: 350px;
        }
        @include mobile {
            font-size: 14px;
          }
    }

    // .services-item__image

    &__image {
        min-width: 110px;
        height: 100%;
        object-fit: cover;
        @include container {
            min-width: 300px;
        }
        @include mobile {
            min-width: 0;
            width: 142px;
        }
        // .services-item__image_wide

        &_wide {
            min-width: 218px;
            @include container {
                min-width: 300px;
            }
            @include mobile {
                min-width: 0;
                width: 142px;
              }
        }
    }
}