@import "mixins";


.address {
    width: 100%;
    position: relative;
    // .address__body

    &__body {
        width: 100%;
        height: 100%;
       padding: 57px 0;
       @include mobile {
            padding: 80px 0 280px 0 ;
        }
        @include mobile2 {
            padding: 80px 0 140px 0 ;
        }
    }

    // .address__block

    &__block {
        width: 409px;
        background-color: #fff;
        border: 1px solid rgba(110, 110, 110, 0.5);
        border-radius: 4px;
        padding: 50px 40px;
        @include mobile {
            width: 100%;
        }
    }

    // .address__title

    &__title {
        margin: 0 0 47px 0;
        @include mobile {
            font-size: 18px;
        }
    }

    // .address__text

    &__text {
        margin: 0 0 20px 0;
        color: $greyColor;
    }

    // .address__time

    &__time {
        margin: 0 0 20px 0;
        color: $greyColor;
    }

    // .address__telephones

    &__telephones {
        margin: 0 0 38px 0;
        display: flex;
        flex-direction: column;
    }

    // .address__telephone

    &__telephone {
        color: $greyColor;
    }

    // .address__button

    &__button {
        width: 100%;
        @include mobile {
            font-size: 14px;
        }
    }

    // .address__background

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-image: url("../img/address/background.png");
        background-position: center;
        background-size: cover;
        z-index: -50;
    }
}

