@import "mixins";


.portfolio {
    width: 100%;
    background-color: $darkColor;
    padding: 130px 0;
    @include mobile {
        padding: 40px 0 50px 0;
    }
    // .portfolio__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .portfolio__title

    &__title {
        max-width: 660px;
        text-align: center;
        letter-spacing: 0.01em;
        font-weight: 500;
        font-size: 42px;
        margin: 0 0 70px 0;
        color: #fff;
        @include mobile {
           font-size: 24px;
           margin: 0 0 32px 0;
        }
    }

    // .portfolio__content

    &__content {
        width: 100%;
        margin: 0 0 20px 0;
        @include flex-all-sb;
        @include container {
            display: block;
        }
        @include mobile {
            margin: 0 0 10px 0;
         }
    }

    // .portfolio__row

    &__row {
        width: 594px;
        @include container {
            width: 100%;
        }
    }

    // .portfolio__item

    &__item {
        margin-bottom: 60px;
        @include mobile {
            margin-bottom: 40px;
         }
    }

    // .portfolio__button

    &__button {
        width: 215px;
      
        &:hover {
            box-shadow: 0 4px 16px 0 rgba(255, 255, 255, 0.144);
        }
    }
}



.portfolio-item {
    width: 100%;
    padding-bottom: 30px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    @include mobile {
        padding-bottom: 15px;
     }
    // .portfolio-item__image

    &__image {
        height: 500px;
        position: relative;
        margin: 0 0 30px 0;
        
        @include mobile {
            height: 360px;
            margin: 0 0 20px 0;
         }
        @include flex-all-center;
        &:hover {
            .portfolio-item__more {
                opacity: 1;
                visibility: visible;
                @include mobile {
                    opacity: 0;
                    visibility: hidden;
                 }
            }
        }
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            
        }

        // .portfolio-item__image_big

        &_big {
            height: 650px;
            @include container {
                height: 500px;
            }
            @include mobile {
                height: 360px;
             }
        }
    }



    // .portfolio-item__more

    &__more {
        transition: .3s all ease 0s;
        opacity: 0;
        visibility: hidden;
        z-index: 2;
        position: relative;
        @include flex-v-center;
        button {
            color: #fff;
            margin: 0 10px;
            &:hover {
                box-shadow: 0 4px 16px 0 rgba(255, 255, 255, 0.479);
            }
        }
    }

    // .portfolio-item__content

    &__content {
        @include flex-all-sb;
        @include mobile {
            align-items: flex-start;
        }
    }

    // .portfolio-item__left

    &__left {

    }

    // .portfolio-item__address

    &__address {
        color: $lightGreyColor;
        margin: 0 0 15px 0;
        img {
            margin: 0 13px 0 0;
        }
        @include mobile {
           max-width: 187px;
         }
    }

    // .portfolio-item__caption

    &__caption {
        color: #fff;
       
    }

    // .portfolio-item__right

    &__right {

    }

    // .portfolio-item__desc

    &__desc {
        margin: 0 0 11px 0;
        color: $greenColor;
        letter-spacing: 0.05em;
        font-size: 14px;
        @include mobile {
            display: none;
          }
    }

    // .portfolio-item__price

    &__price {
        color: #fff;
    }
}
