.button-white, .button-green, .button-green-full {
    height: 48px;
    background-color: transparent;
    font-weight: 700;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    border-radius: 4px;
    transition: .3s all linear 0s;
}

.button-white {
    color: #fff;
    border: 1px solid rgba(255, 255, 255, 0.5);
}
.button-green {
    color: $greenColor;
    border: 1px solid $greenColor;
    &:hover {
        box-shadow: 0 4px 20px 0 rgba(62, 158, 87, .3);
    }
}

.button-green-full {
    color: #fff;
    border: 1px solid $greenColor;
    background-color: $greenColor;
    &:hover {
        box-shadow: 0 4px 16px 0 rgba(62, 158, 87, .6);
    }
}
