@import "mixins";


.faq {
    width: 100%;
    padding: 130px 0;
    @include mobile {
        padding: 80px 0;
    }
    // .faq__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        @include tablet {
            flex-direction: column;
        }
    }

    // .faq__info

    &__info {
        max-width: 317px;
        @include tablet {
            max-width: 600px;
            margin: 0 0 38px 0;
        }
    }

    // .faq__subheader

    &__subheader {
        color: $greyColor;
        margin: 0 0 50px 0;
        @include mobile {
            font-size: 16px;
         }
    }

    // .faq__title

    &__title {
        color: $darkColor;
        font-weight: 500;
        font-size: 42px;
        line-height: 130%;
        letter-spacing: 0.01em;
        margin: 0 0 30px 0;
        @include mobile {
            margin: 0 0 20px 0;
            font-size: 24px;
         }
    }

    // .faq__text

    &__text {
        @include mobile {
            font-size: 14px;
         }
    }

    // .faq__main

    &__main {
        width: 860px;
        @include container {
            width: 600px;
        }
        @include tablet {
            width: 100%;
        }
    }

    // .faq__answers

    &__answers {
        margin: 0 0 50px 0;
        border-top: 1px solid $lightGreyColor;
        @include mobile {
            margin: 0 0 40px 0;
        }
    }

    // .faq__item

    &__item {
    }

    // .faq__button

    &__button {
        width: 363px;
        @include mobile {
            width: 100%;
        }
    }
}




.faq-item {
    width: 100%;
    border-bottom: 1px solid $lightGreyColor;
   

    // .faq-item__top

    &__top {
        cursor: pointer;
        position: relative;
        margin: 28px 0 24px 0;
        @include mobile {
            margin: 20px 0 20px 0;
        }
        width: 100%;
        &::before,
        &::after {
            position: absolute;
            background-color: #212934;
            right: 6px;
            top: 10px;
            content: "";
            cursor: pointer;
            width: 20px;
            height: 2px;
            transition: .2s all ease 0s;
        }
        &::before {
    
        }
        &::after {
            transform: rotate(-90deg);
        }
        &.active {
            &::after {
                transform: rotate(0deg);
            }
        }
    }

    // .faq-item__title

    &__title {
        max-width: 790px;
        margin: 0 40px 0 0;
       @include mobile {
           font-size: 18px;
       }
    }

    // .faq-item__text

    &__text {
        display: none;
        margin: 0 40px 31px 0;
        @include mobile {
            font-size: 14px;
            margin: 0 40px 20px 0;
        }
    }
}
