@import "mixins";


.cripto {
    width: 100%;
    background-color: #000;
    padding: 20px 0;
    @include mobile {
        padding: 0;
        padding-top: 60px;
    }
    // .cripto__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        @include mobile {
           display: block;
        }
    }

    // .cripto__content

    &__content {
        max-width: 580px;
    }

    // .cripto__title

    &__title {
        letter-spacing: 0.01em;
        color: #fff;
        font-size: 42px;
        line-height: 130%;
        margin: 0 0 30px 0;
        @include mobile {
            margin: 0 0 20px 0;
            font-size: 24px;
        }
    }

    // .cripto__text

    &__text {
        color: #fff;
        @include mobile {
            font-size: 14px;
        }
    }

    // .cripto__image

    &__image {
        @include tablet {
            width: 350px;
        }
        @include mobile {
            width: 100%;
            max-height: 500px;
        }
    }
}
