@import "mixins";


.header.dark {
    background-color: $darkColor;
    height: 77px; 
}

.header.header-fixed {
    background-color: rgba(58, 58, 58, .9);
    position: fixed ;
    animation-name: show_header ;
    animation-duration: 1s ;
    margin: 0 ;
}

.header {
    width: 100%;
    height: 94px;
    background-color: transparent;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 50;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    transition: .3s all linear 0s;
    // .header__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .header__left

    &__left {
        @include flex-v-center;
        width: 45%;
    }

    // .header__right

    &__right {
        @include flex-v-center;
        justify-content: flex-end;
        width: 45%;
    }

    // .header__burger

    &__burger {
        position: relative;
        width: 32px;
        height: 19px;
        z-index: 3;
        cursor: pointer;
        margin: 0 46px 0 0;
        @include mobile {
            width: 27px;
            height: 22px;
        }
        &::after,
        &::before {
            position: absolute;
            content: "";
            display: block;
            background-color: #fff;
            width: 100%;
            height: 1px;
            left: 0;
            transition: .2s all 0s linear;
        }
        &::after {
            top: 0;
        }
        &::before {
            bottom: 0;
        }
        span {
            position: absolute;
            display: block;
            width: 22px;
            height: 1px;
            left: 0;
            top: 9px;
            background-color: #fff;
            @include mobile {
                width: 19px;
                top: 11px;
            }
        }
        // .header__burger_active

        &_active {
            &::after {
                transform: rotate(-45deg);
                top: 9px;
                @include mobile {
                    top: 11px;
                }
            }
            &::before {
                transform: rotate(45deg);
                bottom: 9px;
                @include mobile {
                    bottom: 11px;
                }
            }
            span {
                display: none;
            }
            
        }
    }

    // .header__language

    &__language {
        z-index: 3;
        position: relative;
        @include mobile {
            display: none;
        }
    }

    // .header__center

    &__center {
        display: flex;
        align-items: center;
        width: 10%;
    }

    // .header__logo

    &__logo {
        z-index: 3;
        position: relative;
        // .header__logo_desktop

        &_desktop {
            @include mobile {
                display: none;
            }
        }

        // .header__logo_mobile

        &_mobile {
        }
    }

    // .header__socialnetworks

    &__socialnetworks {
        z-index: 3;
        position: relative;
    }

    // .header__social

    &__social {
        cursor: pointer;
        @include tablet2 {
            display: none;
        }
        &:first-child {
            margin: 0 27px 0 0;
            @include container {
                margin: 0 10px 0 0;
            }
        }
    }

    // .header__phone

    &__phone {
        z-index: 3;
        position: relative;
        margin: 0 0 0 75px;
        @include container {
            margin: 0 0 0 15px;
        }
        @include mobile {
            margin: 0;
        }
    }

    // .header__signin

    &__signin {
        z-index: 3;
        position: relative;
        margin: 0 0 0 70px;
        @include container {
            margin: 0 0 0 25px;
        }
        @include mobile {
            margin: 0 0 0 40px;
        }
    }
}

.header-phone {

    // .header-phone__ico

    &__ico {

        // .header-phone__ico_desktop

        &_desktop {
            @include mobile {
                display: none;
            }
        }

        // .header-phone__ico_mobile

        &_mobile {
           
        }
    }

    // .header-phone__number

    &__number {
        margin: 0 0 0 12px;
        font-size: 15px;
        letter-spacing: 0.03em;
        color: #fff;
        @include tablet2 {
            margin: 0 0 0 5px;
        }
        @include mobile {
            display: none;
        }
    }
}



.header-signin {

    // .header-signin__user

    &__user {
        
        // .header-signin__user_desktop

        &_desktop {
            @include mobile {
                display: none;
            }
        }

        // .header-signin__user_mobile

        &_mobile {
        }
    }

    // .header-signin__text

    &__text {
        margin: 0 0 0 15px;
        font-size: 15px;
        letter-spacing: 0.03em;
        color: #fff;
        @include tablet2 {
            margin: 0 0 0 5px;
        }
        @include mobile {
            display: none;
        }
    }
}



