@import "mixins";


.blog {
    width: 100%;
    padding: 130px 0;
    @include mobile {
        padding: 80px 0;
    }
    // .blog__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .blog__top

    &__top {
        width: 100%;
        display: flex;
        margin: 0 0 70px 0;
        form {
            width: 30%;
            @include tablet2 {
               display: none;
            }
            display: flex;
            justify-content: flex-end;
        }
        @include tablet2 {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin: 0 0 30px 0;
        }
    }

    // .blog__subheader

    &__subheader {
        color: $greyColor;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        width: 30%;
        @include tablet2 {
            width: auto;
            margin: 0 0 20px 0;
         }
         @include mobile {
            font-size: 14px;
        }
    }

    // .blog__title

    &__title {
        font-weight: 500;
        font-size: 42px;
        line-height: 130%;
        text-align: center;
        letter-spacing: 0.01em;
        width: 40%;
        @include tablet2 {
            width: auto;
         }
         @include mobile {
            font-size: 24px;
        }
    }

    // .blog__button

    &__button {
        width: 226px;
        @include tablet2 {
            display: none;
        }
    }

    // .blog__buttonmobile

    &__buttonmobile {
        display: none;
        @include tablet2 {
            display: block;
            width: 100%;
            @include flex-all-center;
            margin: 40px 0 0 0;
            button {
                width: 226px;
            }
        }
    }


    // .blog__slider

    &__slider {
        width: 100%;
    }
}


// Slider
.blog-slider {
    position: relative;
    // .blog-slider__item

    &__item {
        max-width: 315px;
        margin: 0 90px 0 0;
        min-height: 550px;
        border-bottom: 1px solid $lightGreyColor;
        @include container {
            max-width: 400px;
        }
        @include tablet2 {
            max-width: 100%;
            margin: 0 15px 0 0;
        }
    }

    // .blog-slider__image

    &__image {
        width: 100%;
        height: 420px;
        margin: 0 0 30px 0;
        object-fit: cover;
    }

    // .blog-slider__row

    &__row {
        width: 100%;
        @include flex-all-sb;
        margin: 0 0 15px 0;
    }

    // .blog-slider__type

    &__type {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: $greenColor;
    }

    // .blog-slider__date

    &__date {
        color: $greyColor;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
    }

    // .blog-slider__text

    &__text {
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.02em;
        margin: 0 0 5px 0;
        width: 100%;
    }
}
