@import "mixins";


.stock {
    width: 100%;
    position: relative;
    height: 602px;
    @include mobile {
       height: 566px;
    }
    // .stock__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .stock__slider

    &__slider {
        width: 714px;
        height: 100%;
        @include tablet2 {
            width: 100%;
        }
    }

    // .stock__background

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-image: url("../img/stock/background.png");
        background-position: center;
        background-size: cover;
        z-index: -50;
       
    }
}


// Slilder
.stock-slider .slick-dots{
   @include flex-all-center;
}
.stock-slider .slick-dots li{
    list-style: none;
    margin: 0 8px;
}
.stock-slider .slick-dots button {
    font-size: 0;
    width: 10px;
    height: 10px;
    background-color: #fff;
    border: none;
    outline: none;
    border-radius: 50%;
    cursor: pointer;
}
.stock-slider .slick-dots li.slick-active button {
    background-color: rgba(255, 255, 255, 0.5);
}


.stock-slider {
    background: rgba(58, 58, 58, 0.99);
    // .stock-slider__item

    &__item {
        padding: 68px 0 0px 110px;
        width: 100%;
        height: 540px;
        color: #fff;
        @include mobile {
            height: 490px;
            padding: 111px 30px 0px 30px;
         }
         @include mobile2 {
            height: 490px;
            padding: 60px 30px 0px 30px;
         }
    }

    // .stock-slider__stock

    &__stock {
        margin: 0 0 85px 0;
        color: #fff;
        @include mobile {
            margin: 0 0 30px 0;
         }
    }

    // .stock-slider__content

    &__content {
    }

    // .stock-slider__caption

    &__caption {
        max-width: 492px;
        font-weight: 500;
        font-size: 42px;
        line-height: 130%;
        letter-spacing: 0.01em;
        padding-bottom: 31px;
        margin: 0 0 35px 0;
        border-bottom: 1px solid #fff;
        span {
            color: $greenColor;
        }
        @include mobile {
            font-size: 24px;
            padding-bottom: 20px;
            margin: 0 0 25px 0;
         }
    }

    // .stock-slider__text

    &__text {
        color: #fff;
        margin: 0 0 87px 0;
        @include mobile {
            margin: 0 0 40px 0;
           }
    }

    // .stock-slider__bottom

    &__bottom {
        @include flex-v-center;
        @include mobile {
          flex-direction: column;
         }
    }

    // .stock-slider__button

    &__button {
        width: 269px;
        margin: 0 40px 0 0;
        @include mobile {
           width: 100%;
           margin: 0 0 28px 0;
        }
    }

    // .stock-slider__call

    &__call {
        @include flex-v-center;
    }

    // .stock-slider__telephone

    &__telephone {
        margin: 0 15px 0 0;
        @include mobile {
            margin: 0 10px 0 0;
          }
    }

    // .stock-slider__calltext

    &__calltext {
        @include flex;
        flex-direction: column;
        @include mobile {
           flex-direction: row;
           a {
               margin: 0 6px 0 0;
           }
         }
         @include mobile2 {
            flex-direction: column;
            a {
                margin: 0;
            }
        }
        a {
            color: #fff;
            text-decoration: underline;
        }
        span {
            
        }
    }
}
