// Counter
.slide-counter {
    position: absolute;
    right: 0;
    z-index: 10;
    display: inline-flex;

    &__current, &__total {
        line-height: 20px;
        letter-spacing: 0.01em;
        text-transform: uppercase;
    }
    // .slide-counter__current

    &__current {
        font-weight: 600;
        color: $darkColor;
    }

    // .slide-counter__line

    &__line {
        position: relative;
        margin: 0 2px;
        &:before {
            content: '';
            position: absolute;
            top: 50%;
            right: 0px;
            width: 2px;
            height: 15px;
            background: $greyColor;
            transform: rotate(7deg) translateY(-50%);
        }
    }

    // .slide-counter__total

    &__total {
        font-weight: 700;
        color: $greyColor;
    }
}

