@import "mixins";


.mount {
    width: 100%;
    position: relative;
    // .mount__body

    &__body {
        padding: 118px 0;
        width: 100%;
        height: 100%;
        max-width: 678px;
        @include mobile {
            padding: 50px 0;
        }
    }

    // .mount__title

    &__title {
        letter-spacing: 0.01em;
        font-size: 42px;
        line-height: 130%;
        margin: 0 0 30px 0;
        color: #fff;
        @include mobile {
            font-size: 24px;
            line-height: 130%;
            margin: 0 0 20px 0;
        }
    }

    // .mount__text

    &__text {
        margin: 0 0 30px 0;
        color: #fff;
        @include mobile {
            font-size: 14px;
            line-height: 24px;
        }
    }

    // .mount__button

    &__button {
        width: 235px;
        @include mobile {
           width: 100%;
        }
    }

    // .mount__background

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-image: url("../img/mount/background.png");
        background-position: center;
        background-size: cover;
        z-index: -50;
    }
}