@import "mixins";


.follow {
    width: 100%;
    position: relative;
    // .follow__body

    &__body {
        padding: 65px 0;
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        @include mobile {
            padding: 72px 0 89px 0;
            display: block;
        }
    }

    // .follow__iphone

    &__iphone {
        @include tablet2 {
            display: none;
        }
    }

    // .follow__content

    &__content {
        margin: 0 30px 0 0;
    }

    // .follow__title

    &__title {
        font-weight: 500;
        font-size: 42px;
        line-height: 130%;
        max-width: 423px;
        letter-spacing: 0.01em;
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        color: #fff;
        margin: 0 0 30px 0;
        @include mobile {
           font-size: 24px;
           padding-bottom: 24px;
           max-width: 100%;
           margin: 0 0 24px 0;
        }
    }

    // .follow__subheader

    &__subheader {
        color: #fff;
        margin: 0 0 84px 0;
        @include mobile {
            margin: 0 0 60px 0;
         }
    }

    // .follow__benifits

    &__benifits {
        margin: 0 0 50px 0;
    }

    // .follow__benifit

    &__benifit {
        @include flex-v-center;
        color: #fff;
        margin: 0 0 30px 0;
        img {
            margin: 0 28px 0 0;
        }
    }

    // .follow__links

    &__links {
        @include flex-v-center;
        @include mobile {
           display: block;
         }
    }

    // .follow__link

    &__link {
        width: 204px;
        height: 45px;
        border-radius: 4px;
        border: 1px solid #fff;
        margin: 0 48px 0 0;
        @include flex-all-center;
        color: #fff;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 14px;
        line-height: 20px;
        font-weight: 700;
        @include mobile {
            margin: 0 0 24px 0;
        }
        img {
            margin: 0 14px 0 0;
        }
    }

    // .follow__background

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-image: url("../img/follow/background.png");
        background-position: center;
        background-size: cover;
        z-index: -50;
        // img {
        //     position: absolute;
        //     top: 0;
        //     left: 0;
        //     width: 100%;
        //     height: 100%;
        //     z-index: -50;
        //     object-fit: cover;
        // }
    }
}
