@import "mixins";


.language-change.active {
    .language-change__arrow {
        transform: rotate(180deg);
    }
    .language-change__body{
        display: block;
    }
}
.language-change {
    position: relative;
    // .language-change__header

    &__header {
        @include flex-v-center;
        cursor: pointer;
    }

    // .language-change__current

    &__current {
        margin: 0 10px 0 0;
        font-size: 15px;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #fff;
    }

    // .language-change__arrow

    &__arrow {
        transition: .2s all linear 0s;
    }

    // .language-change__body

    &__body {
        display: none;
        background-color: #fff;
        margin-top: 10px;
        position: absolute;
        z-index: 51;
        left: 0;
        right: 0;
        top: 100%;
        border-radius: 2px;
    }

    // .language-change__item

    &__item {
        cursor: pointer;
        color: $darkColor;
        width: 100%;
        padding: 3px 5px;
        transition: .2s all ease 0s;
        &:hover {
            background-color: $greyColor;
            color: #fff;
        }
    }
}
