@import "mixins";


.calculate {
    width: 100%;
    background-color: $darkColor;
    padding: 130px 0;
    // .calculate__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        align-items: flex-start;
        @include container {
            display: block;
        }
        
    }

    // .calculate__left

    &__left {
    }

    // .calculate__title

    &__title {
        color: #fff;
        margin: 0 0 48px 0;
        @include mobile {
            margin: 0 0 40px 0;
        }
    }

    // .calculate__form

    &__form {
        @include container {
           margin: 0 0 50px 0;
        }
    }


    // .calculate__block

    &__block {
        width: 553px;
        @include container {
            width: 100%;
         }
    }

}


// Form
.calculate-form {

    // .calculate-form__input

    &__input {
        border-radius: 4px;
        outline: none;
        width: 204px;
        height: 40px;
        background: rgba(0, 0, 0, 0.3);
        border: 1px solid rgba(194, 194, 194, 0.3);
        color: #fff;
        text-align: center;
        letter-spacing: 0.02em;
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
            -webkit-appearance: none;
        }
        -moz-appearance: textfield;
         margin: 0 0 0 15px;
         @media (max-width: 400px) {
           width: 150px;
        }
    }

    // .calculate-form__meters

    &__meters {
        color: #fff;
        margin: 0 0 0 15px;
        @include mobile {
            font-size: 14px;
        }
        @media (max-width: 400px) {
            margin: 0 0 0 5px;
        }
    }

    // .calculate-form__line

    &__line {
        @include flex-v-center;
        margin: 0 0 27px 0;
        @include mobile {
            margin: 0 0 35px 0;
        }
    }

    // .calculate-form__square

    &__square {
        font-weight: 400;
        color: #fff;
        margin: 0 34px 0 0;
        @media (max-width: 400px) {
            margin: 0 5px 0 0;
        }
       
    }

    // .calculate-form__accomodation

    &__accomodation {
        margin: 0 0 25px 0;
    }

    // .calculate-form__caption

    &__caption {
        color: #fff;
        font-weight: 400;
        margin: 0 0 10px 0;
        @include mobile {
            font-size: 14px;
        }
    }

    // .calculate-form__row

    &__row {
        @include flex-v-center;
        @include mobile {
            display: none;
        }
    }

    // .calculate-form__item

    &__item {
        margin: 0 15px 0 0;
    }

    // .calculate-form__service

    &__service {
    }
}



// Item
.calculate-item.active {
    background-color: $greenColor;
    .calculate-item__check span {
        display: block;
    }
    .calculate-item__image {
        display: none;
    }
    .calculate-item__image_white {
        display: block;
    }
}


.calculate-item {
    width: 204px;
    height: 116px;
    border-radius: 4px;
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(194, 194, 194, 0.3);
    position: relative;
    cursor: pointer;
    padding: 0 0 0 22px;
    @include flex-v-center;
    // @include mobile {
    //     min-width: 35%;
    // }
    // .calculate-item__check

    &__check {
        position: absolute;
        background-color: transparent;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        top: 8px;
        right: 8px;
        border: 1px solid #fff;
        @include flex-all-center;
        span {
            width: 8px;
            height: 8px;
            background-color: #fff;
            border-radius: 50%;
            display: none;
        }
        
    }

    // .calculate-item__content

    &__content {
    }

    // .calculate-item__image

    &__image {
        margin: 0 0 10px 0;
        // .calculate-item__image_white

        &_white {
            display: none;
        }
    }

    // .calculate-item__caption

    &__caption {
        color: #fff;
        align-items: center;
        letter-spacing: 0.02em;
        font-weight: 500;
        font-size: 14px;
        line-height: 120%;
    }
}




// Block
.calculate-block {
    border-radius: 4px;
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(194, 194, 194, 0.3);
    padding: 55px 55px 47px 55px;
    
    // .calculate-block__top

    &__top {
        width: 100%;
        border-bottom: 1px solid #C2C2C2;
    }

    // .calculate-block__item

    &__item {
        width: 100%;
        @include flex-all-center;
        flex-direction: column;
        margin: 0 0 40px 0;
    }

    // .calculate-block__caption

    &__caption {
        color: #fff;
        font-weight: 400;
        text-align: center;
        margin: 0 0 20px 0;
    }

    // .calculate-block__price

    &__price {
        color: #fff;
        text-align: center;
    }

    // .calculate-block__form

    &__form {
        margin: 20px 0 0 0;
        @include flex-all-center;
        flex-direction: column;
        width: 100%;
    }

    // .calculate-block__checkbox

    &__checkbox {
        margin: 0 0 22px 0;
        div {
            letter-spacing: 0.02em;
            color: #fff;
            padding-left: 40px;
            width: auto;
            @include mobile {
                font-size: 14px;
            }
        }
    }

     // .calculate-block__button

     &__button {
         width: 100%;
         @include mobile {
             font-size: 14px;
         }
    }
}



//Slider
.calculate-slider {
    display: none;
    @include mobile {
        display: block;
    }
}