@import "mixins";


.benifits {
    width: 100%;
    padding: 100px 0 ;
    @include mobile {
        padding: 80px 0 ;
    }
    // .benifits__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-v-center;
        flex-direction: column;
    }

    // .benifits__title

    &__title {
        text-align: center;
        margin: 0 0 100px 0;
        color: $darkColor;
        letter-spacing: 0.01em;
        font-size: 42px;
        line-height: 130%;
        font-weight: 500;
        @include mobile {
            font-size: 24px;
            margin: 0 0 40px 0;
        }
    }

    // .benifits__content

    &__content {
        width: 100%;
        margin: 0 0 20px 0;
        @include flex-all-sb;
        flex-wrap: wrap;
        @include mobile {
            margin: 0 0 10px 0;
        }
    }

    // .benifits__item

    &__item {
        width: 33.333%;
        max-width: 373px;
        margin: 0 0 80px 0;
        @include container {
            width: 50%;
            max-width: 47%;
        }
        @include mobile {
            width: 100%;
            max-width: 100%;
            margin: 0 0 30px 0;
        }
    }

    // .benifits__image

    &__image {
        margin: 0 0 30px 0;
        @include mobile {
            margin: 0 0 20px 0;
        }
    }

    // .benifits__caption

    &__caption {
        margin: 0 0 30px 0;
        @include mobile {
            margin: 0 0 10px 0;
        }
    }

    // .benifits__text

    &__text {
        letter-spacing: 0.02em;
        color: $darkColor;
        font-size: 14px;
        line-height: 22px;
        max-width: 500px;
    }

    // .benifits__button

    &__button {
        width: 423px;
        @include mobile {
            width: 100%;
            max-width: 330px;
        }
    }
}
