@import "mixins";


.first-screen {
    width: 100%;
    height: 100vh;
    position: relative;
    padding: 120px 0 30px 0;
    @media (max-height: 450px) {
        min-height: 100vh;
        height: auto;
    }
    // .first-screen__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    // .first-screen__title

    &__title {
        color: #fff;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 72px;
        line-height: 120%;
        max-width: 571px;
        margin: 0 0 9px 0;
        @include mobile {
            letter-spacing: 0.01em;
            font-size: 32px;
            line-height: 130%;
            margin: 0 0 15px 0;
        }
    }

    // .first-screen__subtitle

    &__subtitle {
        max-width: 571px;
        color: #fff !important;
        margin-bottom: 30px;
       
    }

    // .first-screen__block

    &__block {
        display: flex;
        max-width: 970px;
        @include mobile {
           max-width: 100%;
           width: 100%;
        }
    }

    // .first-screen__works

    &__works {
        position: relative;
        right: -2px;
        width: 70%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        border-top: 2px solid #fff;
        @include mobile {
            border: none;
            width: auto;
        }
        span {
            color: #FFFFFF;
            text-align: center;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 14px;
            position: relative;
            margin: 0 50px;
            transition: .2s all ease 0s;
            @include mobile {
               margin: 0;
               margin: 0 40px 0 0;
            }
          
            &::before {
                position: absolute;
                bottom: -15px;
                left: 0;
                width: 128px;
                height: 1px;
                background-color: #fff;
                content: "";
                transition: .2s all ease 0s;
                @include mobile {
                    bottom: -5px;
                }
                @media (max-width: 370px) {
                    display: none;
                }
            }
        }
    }

    // .first-screen__button

    &__button {
        width: 195px;
        color: $darkColor;
        background-color: #fff;
        cursor: pointer;
        @include mobile {
            width: 160px;
        }
        &:hover {
            box-shadow: 0 4px 16px 0 rgba(255, 255, 255, 0.397);
        }
    }

    // .first-screen__background

    &__background {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-image: url("../img/first-screen/background.png");
        background-position: center;
        background-size: cover;
        z-index: -50;
       
    }
}
