@import "mixins";


.youtube {
    width: 100%;
    padding: 167px 0 124px 0;
   @include mobile {
        padding: 80px 0;
   }
    // .youtube__body

    &__body {
        min-width: 100%;
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        @include tablet2 {
            flex-direction: column-reverse;
            align-items: flex-start;
        }
    }

    // .youtube__subtitle

    &__subtitle {
        display: none;
        @include tablet2 {
            display: block;
            margin: 0 0 30px 0;
            color: $greyColor;
        }
    }

    // .youtube__main

    &__main {
        max-width: 424px;
        margin: 0 30px 0 0;
        @include tablet2 {
            max-width: 100%;
            margin: 0;
            
       }
    }

    // .youtube__subheader

    &__subheader {
        margin: 0 0 51px 0;
        @include tablet2 {
          display: none;
       }
    }

    // .youtube__title

    &__title {
        margin: 0 0 30px 0;
        @include mobile {
            font-size: 24px;
            margin: 0 0 20px 0;
       }
    }

    // .youtube__text

    &__text {
        margin: 0 0 50px 0;
        @include mobile {
            margin: 0 0 30px 0;
       }
    }

    // .youtube__button

    &__button {
        @include mobile {
            width: 100%;
       }
        width: 309px;
        height: 48px;
        color: #fff;
        background-color: #E32404;
        font-weight: 700;
        font-size: 14px;
        letter-spacing: 0.08em;
        cursor: pointer;
        border-radius: 4px;
        transition: .3s all linear 0s;
        &:hover {
            box-shadow: 0 4px 20px 0 rgba(227, 36, 4, .5);
        }
    }

    // .youtube__slider

    &__slider {
        max-width: 642px;
        @include tablet2 {
            max-width: 100%;
            width: 100%;
            margin: 0 0 105px 0;
       }
    }

    // .youtube__counter

    &__counter {
        bottom: -50px;
    }
}



// Slider
.youtube-slider .slick-arrow{
    position: absolute;
    bottom: -50px;
    z-index: 10;
    font-size: 0;
    outline: none;
    border: none;
    width: 45px;
    height: 10px;
    cursor: pointer;
    transition: .2s all linear 0s;
    @include desktop {
        &:hover {
            transform: translateY(-1px);
        }
    }
}
.youtube-slider .slick-arrow.slick-prev{
    left: 0;
    background: url("../img/youtube-left-arrow.svg") 0 0 no-repeat;
}
.youtube-slider .slick-arrow.slick-next{
    left: 90px;   
    background: url("../img/youtube-right-arrow.svg") 0 0 no-repeat;
}

.youtube-slider {
    position: relative;
    // .youtube-slider__item

    &__item {
        height: 426px;
        width: 100%;
    }
}


